div.theme-dropdown {
  width: 15%;
  height: 30%;
  position: absolute;
  bottom: 10%;
  display: flex;
}
@media screen and (max-width: 680px) {
  div.theme-dropdown {
    width: 95%;
    height: 90%;
    top: 1%;
    left: 2%;
    right: 2%;
  }
}
div.theme-dropdown#colourblind {
  right: 7%;
}
div.theme-dropdown#theme {
  right: 20%;
}
